import { InAppBrowser } from '@ionic-native/in-app-browser';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController, ModalController } from 'ionic-angular';
import { BackProvider } from '../../providers/back/back';
import { Network } from '@ionic-native/network';
import { AngularFireAuth } from 'angularfire2/auth';
import { AuthProvider } from '../../providers/auth/auth';
import { DataProvider } from '../../providers/data/data';
import { MoodRate, Collections } from '../../app/app.models';
import moment from 'moment';

@IonicPage()
@Component({
  selector: 'page-home',
  templateUrl: 'home.html',
})
export class HomePage {

  public notRead: number = 0
  private uid: string = localStorage.getItem('uid')
  private modalOpened = false;

  public tempranoUnlock: string[] = [
    'MediaPage',
    'ForosPage',
    'AsesoriasPage',
    'SimuladoresPage'
  ]

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public back: BackProvider,
    private toast: ToastController,
    private modal: ModalController,
    private iap: InAppBrowser,
    private network: Network,
    private afAuth: AngularFireAuth,
    public auth: AuthProvider,
    private data: DataProvider,
  ) {
  }

  ionViewDidLoad() {

    this.loadNotis()
    this.checkForInternet()

    this.auth.user$.subscribe(user => {
      if (user) {
        this.loadMoodRates(user.uid)
      }
    })

  }

  async loadMoodRates(uid: string) {

    const rates = await this.data.getCollectionQuery<MoodRate>(Collections.MOOD_RATE, ref => ref.where('user', '==', uid))
    /* const dailyRegisters = await this.data.getCollectionQuery<MoodRate>(Collections.DAILY_REGISTER, ref => ref.where('user', '==', uid)) */
    const today = moment().format('DD-MM-YYYY')
    const canOpen = rates.filter(r => r.date == today).length <= 0
    /* const canOpenDaily = rates.filter(r => r.date == today).length <= 0 */

    console.log(rates, today)

    if (canOpen && !this.modalOpened) {
      this.modal.create('ComoTeSientesHoyPage').present();
      this.modalOpened = true;
    }

    /* if (canOpenDaily) this.openDaily(uid, today) */

  }

  openNotis() {
    const m = this.modal.create('NotificationsPage')
    m.present()
    m.onDidDismiss(data => this.loadNotis())
  }

  loadNotis() {
    this.back.getNotisUser(this.uid).subscribe(data => {
      this.notRead = data.filter(n => !n.read).length
    }, err => /* this.toast.create({message: 'No se pudo cargar la cantidad de notificaciones no leídas...', duration: 2000}).present() */ null)
  }

  checkForInternet() {
    this.network.onDisconnect().subscribe(() => {
      console.log('no connection');

    })
  }

  reload(r) {
    setTimeout(() => r.complete(), 1000)
  }

  openChecklist() {
    this.navCtrl.push('ChecklistPage')
  }

  goto(page: string) {

    this.navCtrl.push(page)

    /* if (this.back.isTemprano && this.tempranoUnlock.indexOf(page) >= 0) {
      this.navCtrl.push(page)
    } else if (page == 'lock') {
      this.toast.create({message: 'Disponible en Abril', duration: 2000}).present()
    } else if (!this.back.isTemprano && this.tempranoUnlock.indexOf(page) >= 0) {
      this.toast.create({message: 'Disponible en Abril', duration: 2000}).present()
    } else {
      this.navCtrl.push(page)
    } */

  }

  openVideos(isPrograma: boolean = false) {
    // EspecialidadesPage
    if (this.auth.isTemprano) return this.navCtrl.push('EspecialidadesPage', {isPrograma})
    return this.toast.create({message: 'Disponible en Abril', duration: 2000}).present()
    //this.toast.create({message: 'Debes adquirir el curso premium.', duration: 2000}).present()

  }

  async logout() {

    /* localStorage.removeItem('user')
    localStorage.removeItem('token')
    localStorage.removeItem('uid')
    this.navCtrl.setRoot('LoginPage') */

    await this.afAuth.auth.signOut()
    this.navCtrl.setRoot('LoginPage')

  }

  openZamna(link: string) {
    this.iap.create('https://zamnademy.com').show()
  }

  purge() {
    this.back.purgeGoogle().subscribe(data => console.log(data), err => console.log(err))
  }

  openCalendar() {

    if (this.auth.isSmartCalendar && !this.auth.user.noCalendar) return this.navCtrl.push('SmartCalendarPage');
    this.navCtrl.push('CalendarPage');

  }

}



